<template>
  <div>
    <van-nav-bar title="个人信息" @click-left="Return" class="bgc">
      <van-icon name="arrow-left" slot="left" />
    </van-nav-bar>

    <div class="content">
      <van-cell title="头像" center colon>
        <div class="clearfix">
          <div class="fr user-img">
            <van-image width="100" height="100" :src="user.userImg" />
            <van-uploader max-count="1" :after-read="afterRead">
              <van-button type="primary">上传图片</van-button>
            </van-uploader>
          </div>
        </div>
      </van-cell>
      <van-cell class="v-cell" title="用户id" :value="user.userId"></van-cell>
      <van-cell class="v-cell" title="手机号" :value="user.phone"></van-cell>

      <van-cell class="v-cell" title="昵称">
        <div class="clearfix">
          <div class="fr">
            <van-field
              @blur="updateUserInfo({key: 'nickName', value: user.nickName, url: '/updateNickName'})"
              class="v-filed"
              v-model="user.nickName"
              input-align="right"
            />
          </div>
        </div>
      </van-cell>
      <van-cell>
        <div class="clearfix">
          <div class="fl v-cell-text">简介</div>
          <div class="fl v-cell-textarea">
            <textarea
              @change="updateUserInfo({key: 'desc', value: user.desc, url: '/updateDesc'})"
              class="textarea"
              v-model="user.desc"
            ></textarea>
          </div>
        </div>
      </van-cell>
    </div>
  </div>
</template>

<script>
import "../../assets/css/commaint.less";
import "../../assets/css/myinfo.less";

// 导入vuex相关文件
import { mapState, mapMutations } from "vuex";

export default {
  data() {
    return {
      maxSize: 1 * 1024 * 1024,
      user: {}
    };
  },
  computed: {
    ...mapState(["userDataCache"])
  },
  created() {
    //获取用户信息
    this.getUserInfo();
  },
  methods: {
    //   获取用户信息
    ...mapMutations(["changeData"]),
    getUserInfo() {
      let tokenString = localStorage.getItem("__Tk");

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });

      this.axios({
        method: "GET",
        url: "/findAccountInfo",
        params: {
          appkey: this.appkey,
          tokenString
        }
      })
        .then(result => {
          this.$toast.clear();
          //   
          if (result.data.code == "B001") {
            this.user = result.data.result[0];

            // // 缓存数据
            this.changeData({ key: "userDataCache", value: this.user });
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },
    // 返回
    Return() {
      this.$router.push({ name: "My" });
    },

    // 上传头像
    afterRead(file) {
      //获取图片base64
      let serverBase64Img = file.content.replace(
        /^data:image\/[A-Za-z]+;base64,/,
        ""
      );

      //获取图片类型
      let imgType = file.file.type.split("/")[1];

      let tokenString = localStorage.getItem("__Tk");

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });

      this.axios({
        method: "POST",
        url: "/updateAvatar",
        data: {
          appkey: this.appkey,
          tokenString,
          imgType,
          serverBase64Img
        }
      })
        .then(result => {
          this.$toast.clear();

          if (result.data.code == "H001") {
            this.user.userImg = result.data.userImg;
          } else {
            this.$toast(result.data.msg);
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },

    //修改昵称, 简介
    updateUserInfo(o) {
      let tokenString = localStorage.getItem("__Tk");

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });

      this.axios({
        method: "POST",
        url: o.url,
        data: {
          appkey: this.appkey,
          tokenString,
          [o.key]: o.value
        }
      })
        .then(result => {
          this.$toast.clear();
          
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    }
  }
};
</script>

<style lang="less" scoped>
</style>